//course page 
        .course {
            padding-top: 48px;
            padding-bottom: 88px;
            @include table {
                padding-top: 64px;
                padding-bottom: 80px;
            }
            @include pc {
                padding-top: 64px;
                padding-bottom: 113px;
            }
        }
        .course_header_img {
            min-height: 218px;
            background-image: url($bgPath+$course_header_img);
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 0 50%;
            @include table {
                min-height: 341px;
            }
            @include pc {
                min-height: 341px;
            }
        }

        .option-ico {
            border: 0px;
            appearance:none;
            -moz-appearance:none;
            -webkit-appearance:none;
            width: 106px;
            height: 36px;
            background-image: url($bgPath+$options-ico);
            background-size: 6px 10.6px;
            background-repeat: no-repeat;
            background-position: 90% center;
    }
        .card-list {
            
            li {
                padding: 24px;
                padding-top: 106px;
                position: relative;
                background-size: cover;
                background-repeat: no-repeat;
                &:nth-child(1) {
                    background-image: url($bgPath+$course_article_img-1);
                    background-position: 0 70%;
                }
                &:nth-child(2) {
                    background-image: url($bgPath+$course_article_img-2);
                    background-position: 0% 50%;
                }
                &:nth-child(3) {
                    background-image: url($bgPath+$course_article_img-3);
                    background-position: 0% 50%;
                }
                &:nth-child(4) {
                    background-image: url($bgPath+$course_article_img-4);
                    background-position: 0% 50%;
                }
                &:nth-child(5) {
                    background-image: url($bgPath+$course_article_img-5);
                    background-position: 0% 50%;
                }
                &:nth-child(6) {
                    background-image: url($bgPath+$course_article_img-6);
                    background-position: 0% 50%;
                }
                &:nth-child(7) {
                    background-image: url($bgPath+$course_article_img-7);
                    background-position: 0% 50%;
                }
                &:nth-child(8) {
                    background-image: url($bgPath+$course_article_img-8);
                    background-position: 0% 50%;
                }
                .badge {
                    position: absolute;
                    top: -14.5px;
                }
            }
            
        }

